function Event(props: {event: {key: number, title: string, date: Date, description: string}}) :JSX.Element {
  return (
    <div className={`event event-${props.event.key}`}>
     	<h1><span className='title-span'>{props.event.title} - {props.event.date.toDateString().slice(4)}</span></h1>
      <p><span className='p-span'>{props.event.description}</span></p>
    </div>
  );
}

export default Event;
